body {
  background-color: #ffffff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrap {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: stretch;
  flex-direction: row;
}

.wrap i {
  font-size: 200px;
  padding: 13px;
  color: #7b6bd4;
}

body button {
  padding: 1em 3em;
  border: none;
  background-color: #595196;
  color: #fff;
  font-size: 24px;
  border-radius: 12px;
  cursor: pointer;
}

body button:focus {
  outline: none;
}

body button:disabled,
body button[disabled]{
  border: 1px solid #201c3e;
  background-color: #201c3e;
  color: #ffffff;
  cursor: none;
}

.btnwrap {
  display: flex;
  justify-content: center;
}